import styled from "styled-components";
import aboutImage from "../assets/about/aboutImage.png"
import cursorimage from "../assets/about/cursorIcon.png";
import serverImage from "../assets/about/serverIcon.png"
import resume from "../assets/resume/Resume.pdf"
import { DownloadIcon } from "@chakra-ui/icons";

export const About = () => {
    return (
        <ABOUT id="about" className='about section about-main'>
            <div className="content">
            <h2 className="title">About</h2>
            
                {/* <img src={aboutImage} alt="about image" /> */}
                <ul className="aboutItems">
                <li className="aboutItem">
                    <div className="aboutItemText">
                    <p id="user-detail-intro">
                        I'm a frontend developer with experience in building responsive
                        and optimized sites.
                        Having hands-on experience of working with HTML, CSS, JavaScript, React, NodeJS and MongoDB.
                    
                        I have experience developing fast and optimised back-end systems
                        and APIs.
                        I have designed multiple landing pages and have created design
                        systems as well
                    </p>
                    </div>
                </li>
                </ul>
            <button className="contact-btn" type="button" id="resume-button-2">
                      <a id="resume-link-2" href={resume} rel='noopener noreferrer' download="ashwini_titarmare_resume">
                        Resume
                        <DownloadIcon color="#0b2447" />
                      </a>
              </button>
            </div>
        </ABOUT>
    )
}

const ABOUT = styled.div`
    position: relative;
  background-color: rgba(12, 12, 12, 0.6);
  border-radius: 15px;
  margin-top: 129px;
  z-index: 1;
  margin-left: 10%;
  margin-right: 10%;
  

  .title {
  color: var(--color-text);
  font-size: 35px;
  font-weight: 700;
  letter-spacing: 1.75px;
  text-transform: uppercase;
  margin-bottom: 0px;
}
#user-detail-intro {
  width: 80%;
  margin: auto;
  text-align: center;
}
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-image: linear-gradient(
    90deg,
    rgba(165, 215, 232, 0.42) 0%,
    rgba(255, 255, 255, 0) 100%
  );
  background-size: 0 100%;
  background-repeat: no-repeat;
  transition: 0.4s;
  /* padding: 20px; */

  border-radius: 15px;
}

.aboutImage {
  width: 35%;
}
.contact-btn {
      background-color: white;
      border: none;
      font-size: 25px;
      border: 1px solid white;
      border-radius: 5px;
      margin-bottom: 50px;
      color: var(--color-dark);
      padding: 7px 15px;
      font-weight: 600;
      
    }
    .contact-btn a {
      color: var(--color-dark);
      text-decoration: none;

    }
.aboutItems {
  color: var(--color-text);
  display: flex;
  flex-direction: column;
  gap: 50px;
  padding: 10px;
}

.aboutItem {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 20px;
  list-style: none;
  padding: 25px;
}

.content:hover {
  background-size: 100% 100%;
}

.aboutItem h3 {
  font-size: 25px;
  font-weight: 600;
}

.aboutItem p {
  font-size: 25px;
  text-align: center;
}

@media screen and (max-width: 1300px) {
  /* .container {
    background-color: transparent;
    padding-left: 0;
    padding-right: 0;
  } */

  .content {
    flex-direction: column;
  }

  .aboutItems {
    margin-top: 29px;
  }

  .aboutItem {
    padding-left: 0;
    padding-right: 0;
  }
}
@media screen and (max-width: 350px) {
  .aboutItems p {
    width: 80%;
  }
}
`