import styled from "styled-components";
import htmlImg from "../assets/skills/html.png"
import cssImg from "../assets/skills/css.png";
import reactImg from "../assets/skills/react.png";
import mongoImg from "../assets/skills/mongodb.png";
import nodeImg from "../assets/skills/node.png";
import chakraImg from "../assets/skills/chakra-ui.png";
import cypressImg from "../assets/skills/cypress.png";
import expressImg from "../assets/skills/express.png";
import gitImg from "../assets/skills/git.png";
import github from "../assets/skills/github.png";
import js from "../assets/skills/javascript.png";
import jest from "../assets/skills/jest_testing.png";
import material from "../assets/skills/material.png";
import mongoose from "../assets/skills/mongoose.png";
import npm from "../assets/skills/npm.png";
import postman from "../assets/skills/postman.png";
import redux from "../assets/skills/redux.png";
import sass from "../assets/skills/sass.png";
import styles from "../assets/skills/styled.png";
import typescript from "../assets/skills/typescript.png"

export const Skills = () => {
    return (
        <SKILL className="container" id="skills">
            <h2 className="title">SKILLS</h2>
            <div className="tech">
              <h3 className="head">Tech-stacks</h3>
            <div className="content">
                <div className="skills">
                    <Skill className="skill skills-card">
                        <div className="skillImageContainer">
                        <img className="skills-card-img" src={htmlImg} alt={"HTML"} />
                        <p className="skills-card-name">HTML 5</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer skills-card-img">
                        <img className="skills-card-img"  src={cssImg} alt={"CSS"} />
                        <p>CSS 3</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={reactImg} alt={"REACT"} />
                        <p>REACT</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={mongoImg} alt={"mongoDB"} />
                        <p>Mongo DB</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={nodeImg} alt={"NODE"} />
                        <p>Node js</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={expressImg} alt={"NODE"} />
                        <p>Express js</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={js} alt={"NODE"} />
                        <p>Java Script</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={typescript} alt={"NODE"} />
                        <p>Type Script</p>
                        </div>
                    </Skill>
                </div>
                </div>
            </div>
            <div className="tech">
            <h3 className="head">UI-Framework</h3>
            <div className="content">
                <div className="skills">
                    <Skill className="skill skills-card">
                        <div className="skillImageContainer">
                          <img className="skills-card-img" src={sass} alt={"HTML"} />
                          <p className="skills-card-name">Sass</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer skills-card-img">
                        <img className="skills-card-img"  src={chakraImg} alt={"CSS"} />
                        <p>Chakra UI</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={styles} alt={"REACT"} />
                        <p>Styled Components</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={material} alt={"mongoDB"} />
                        <p>Material UI</p>
                        </div>
                    </Skill>
                </div>
                </div>
            </div>
            <div className="tech">
            <h3 className="head">Tech-tools</h3>
            <div className="content">
                <div className="skills">
                    <Skill className="skill skills-card">
                        <div className="skillImageContainer">
                        <img className="skills-card-img" src={redux} alt={"HTML"} />
                        <p className="skills-card-name">Redux</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer skills-card-img">
                        <img className="skills-card-img"  src={mongoose} alt={"CSS"} />
                        <p>Mongoose</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={gitImg} alt={"REACT"} />
                        <p>Git</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={github} alt={"mongoDB"} />
                        <p>Git Hub</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={npm} alt={"NODE"} />
                        <p>NPM</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={cypressImg} alt={"NODE"} />
                        <p>Cypress Testing</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={jest} alt={"NODE"} />
                        <p>Jest Testing</p>
                        </div>
                    </Skill>
                    <Skill className="skill">
                        <div className="skillImageContainer">
                        <img className="skills-card-img"  src={postman} alt={"NODE"} />
                        <p className="tech-name">Postman</p>
                        </div>
                    </Skill>
                </div>
                </div>
            </div>
           
        </SKILL>
    )
}

const SKILL = styled.div`
     color: var(--color-text);
  margin-top: 79px;
  margin-left: 10%;
  margin-right: 10%;
 

  .title {
  color: var(--color-text);
  font-size: 35px;
  font-weight: 700;
  letter-spacing: 1.75px;
  text-transform: uppercase;
  text-align: center;
  }
.tech {
  width: 80%;
  background-color: var(--color-bg);
  padding: 50px;
  border-radius: 20px;
  margin: 30px auto;
}

.content {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 14px;
}
.head {
  text-align: center;
  font-size: 35px;
}
.skills {
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 37px;
}

.skill {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 11px;
  
}



@media screen and (max-width: 830px) {
  .content {
    flex-direction: column;
    align-items: center;
    gap: 34px;
  }

  .skills {
    width: 100%;
    flex-direction: row;
    justify-content: center;
  }
}
`
const Skill = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--color-secondary);
  padding: 15px;
  border-radius: 10px;
  gap: 11px;
  cursor: pointer;
  position: relative;
  transition: transform 0.5s ease;

  .skillImageContainer {
   
    position: relative;

    img {
      display: block;
      width: 75px;
      height: auto;
      opacity: 1;
      transition: opacity 0.5s ease;
    }

    p {
      font-size: 25px;
      font-family: var(--primary-font);
      font-weight: 500;
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      opacity: 0;
      transition: opacity 0.5s ease;
    }
  }

  &:hover {
    background-color: transparent;
    .skillImageContainer img {
      opacity: 0;
    }

    .skillImageContainer p {
      display: block;
      opacity: 1;
    }
  }
`;